.detailImg-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 30px;
}
.detailImg{
  width: 400px;
  border: 20px solid rgba(176, 174, 168, 0.217);
  border-radius: 30px;
}

.detail-container{
  margin: 20px;
  font-weight: lighter;
}

.title{
  display: flex;
  justify-content: center;
  font-size: 30px;
  font-weight: 900;
  font-style: italic;
  margin-bottom: 30px;
}

.table-wrap{
  margin: 40px;
  display: flex;
  justify-content: center;
}
.table{
  width: 80%;
  text-align: center;
}

thead{
  font-size: 19px;
  font-weight: bold;
  background-color: rgba(233, 191, 85, 0.305);
}