.total-container{
  display: flex;
  justify-content: center;
}
.card-container{
  margin: 25px;
  margin-bottom: 40px;
  padding: 30px;
  /* border: 2px solid rgba(95, 95, 95, 0.55); */
  border-radius: 30px;
  width: 1000px;
  background-color: rgba(248, 220, 200, 0.41);
}

.card-title{
  display: flex;
  justify-content: center;
  font-size: 30px;
  font-weight: bold;
  font-style: italic;
  margin-bottom: 20px;
}

.card-wrap{
  display: flex;
}

.img-container{
  margin: 10px;
}

.img{
  width: 300px;
  margin: 10px;
  border-radius: 20px;
}

.summary-container{
  margin: 10px;
}

.rating-container{
  margin: 10px;
  padding-bottom: 30px;
}